html,
body {
  border: 0;
  font-family: "Helvetica-Neue","Helvetica",Arial,sans-serif;
  line-height: 1.5;
  margin: 0;
  padding: 0;
}

div,
span,
object,
iframe,
img,
table,
caption,
thead,
tbody,
tfoot,
tr,
tr,
td,
article,
aside,
canvas,
details,
figure,
hgroup,
menu,
nav,
footer,
header,
section,
summary,
mark,
audio,
video {
  border: 0;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
address,
cit,
code,
del,
dfn,
em,
ins,
q,
samp,
small,
strong,
sub,
sup,
b,
i,
hr,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
legend,
label {
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  margin: 0;
  padding: 0;
}

article,
aside,
canvas,
figure,
figure img,
figcaption,
hgroup,
footer,
header,
nav,
section,
audio,
video {
  display: block;
}

table {
  border-collapse: separate;
  border-spacing: 0;
}

table caption,
table th,
table td {
  text-align: left;
  vertical-align: middle;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

a img {
  border: 0;
}

:focus {
  outline: 0;
}

* {
  -webkit-box-sizing: border-box;
}

textarea {
  resize: none;
  -webkit-appearance: none;
}

input {
  -webkit-appearance: none;
}

select {
  -webkit-appearance: none;
  background-color: #fff;
}

@font-face {
  font-family: 'iconfont';
  src: url('//at.alicdn.com/t/font_1467357626_5109937.eot');
  src: url('//at.alicdn.com/t/font_1467357626_5109937.eot?#iefix') format('embedded-opentype'),url('//at.alicdn.com/t/font_1467357626_5109937.woff') format('woff'),url('//at.alicdn.com/t/font_1467357626_5109937.ttf') format('truetype'),url('//at.alicdn.com/t/font_1467357626_5109937.svg#iconfont') format('svg');
}

.sidebar-mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  z-index: 5;
  background: rgba(0,0,0,0.7);
}


